import { React } from 'react';

import './SingleImage.css';

const SingleImage = ({ image, author, arrowSetter }) => {
  return (
    <div className='SingleImage'>
      <img src={image} alt={author} className='SingleImage__image' />
      <h4 className='SingleImage__author'>{author}</h4>
    </div>
  );
};

export default SingleImage;
