import { React, useState, useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  CellMeasurer,
  CellMeasurerCache,
  createMasonryCellPositioner,
} from 'react-virtualized';
import ImageMeasurer from 'react-virtualized-image-measurer';
import MasonryComponent from './MasonryComponent.js';

import './Images.css';

function debounce(fn, ms) {
  let timer;
  return (_) => {
    clearTimeout(timer);
    timer = setTimeout((_) => {
      timer = null;
      fn.apply(this, arguments);
    }, ms);
  };
}

// Render your grid
const Images = ({ dataSetter, arrowSetter }) => {
  const dispatch = useDispatch();
  const selectImages = useSelector((state) => state.images);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [pages, setPages] = useState(1);

  const [noCacheList, setNoCacheList] = useState({ images: [] });

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  const columnWidth = 300;
  const defaultHeight = 250;
  const defaultWidth = columnWidth;
  // Default sizes help Masonry decide how many images to batch-measure
  const cache = new CellMeasurerCache({
    defaultHeight,
    defaultWidth,
    fixedWidth: true,
  });
  // Our masonry layout will use 3 columns with a 10px gutter between
  const cellPositioner = new createMasonryCellPositioner({
    cellMeasurerCache: cache,
    columnCount: Math.floor(windowWidth / columnWidth),
    columnWidth,
    spacer: 10,
  });

  useEffect(() => {
    const debouncedHandleResize = debounce(() => {
      setWindowWidth(window.innerWidth);
    }, 1000);

    window.addEventListener('resize', debouncedHandleResize);
    return () => {
      window.removeEventListener('resize', debouncedHandleResize);
    };
  });

  //function RESIZE

  const imageSizeDown = (id, downloadLink) => {
    var idNum = parseInt(id);
    var link = '' + downloadLink;

    var width, height;

    if (idNum < 10) {
      width = parseInt(link.substr(27, 4));
      height = parseInt(link.substr(32, 4));
    } else {
      width = parseInt(link.substr(28, 4));
      height = parseInt(link.substr(33, 4));
    }

    var aspectRatio = width / height;

    var newWidth = 300;
    var newHeight = parseInt(newWidth / aspectRatio);

    link = link.replace(width, newWidth);
    link = link.replace(height, newHeight);

    return link;
  };

  const fetchImagesHandler = useCallback(async () => {
    setIsLoading(true);
    setError(null);
    try {
      let api = '';
      api = 'https://picsum.photos/v2/list?page=' + pages;
      const response = await fetch(api);

      if (!response.ok) {
        throw new Error('Something went wrong!');
      }

      const data = await response.json();

      // setNewData([
      //   ...data.map((item) => ({
      //     ...item,
      //     key: item.id,
      //     url: imageSizeDown(item.id, item.download_url),
      //     title: item.author,
      //   })),
      // ]);

      // setNoCacheList([
      //   ...noCacheList,
      //   ...data.map((item) => ({
      //     ...item,
      //     key: item.id,
      //     url: imageSizeDown(item.id, item.download_url),
      //     title: item.author,
      //   })),
      // ]);
      dispatch({
        type: 'addImages',
        images: [
          ...data.map((item) => ({
            ...item,
            key: item.id,
            id: item.id,
            url: imageSizeDown(item.id, item.download_url),
            title: item.author,
          })),
        ],
      });
    } catch (error) {
      setError(error.message);
    }
    setIsLoading(false);
  }, [pages]);

  useEffect(() => {
    fetchImagesHandler();
    arrowSetter(false);
  }, [fetchImagesHandler]);

  useEffect(() => {
    setNoCacheList(selectImages);
  }, [isLoading]);

  // useEffect(() => {
  //   // Handler to call on window resize
  //   function handleResize() {
  //     // Set window width/height to state
  //     setWindowWidth(window.innerWidth);
  //   }
  //   // Add event listener
  //   window.addEventListener('resize', handleResize);
  //   // Call handler right away so state gets updated with initial window size
  //   handleResize();
  //   // Remove event listener on cleanup
  //   return () => window.removeEventListener('resize', handleResize);
  // }, []); // Empty array ensures that effect is only run on mount

  const loadMoreHandler = () => {
    setPages(pages + 1);
  };

  let content = <p>Found no images.</p>;

  if (noCacheList.length > 0) {
    content = (
      <ImageMeasurer
        key={(item) => item.id}
        items={noCacheList}
        image={(item) => item.url}
        defaultHeight={defaultHeight}
        defaultWidth={defaultWidth}
      >
        {({ itemsWithSizes }) => (
          <MasonryComponent
            itemsWithSizes={itemsWithSizes}
            columnWidth={columnWidth}
            defaultHeight={defaultHeight}
            CellMeasurer={CellMeasurer}
            cache={cache}
            dataSetter={dataSetter}
            cellPositioner={cellPositioner}
            windowWidth={windowWidth}
          />
        )}
      </ImageMeasurer>
    );
  }

  if (error) {
    content = <p>{error}</p>;
  }

  if (isLoading) {
    content = <p>Loading...</p>;
  }

  return (
    <div>
      {content}
      <button
        type='button'
        style={{
          backgroundColor: 'white',
          width: '100%',
          height: '100%',
          fontWeight: 'bold',
        }}
        onClick={loadMoreHandler}
      >
        Load More
      </button>
    </div>
  );
};

export default Images;
