import { React, useState } from 'react';
import { useRoutes } from 'react-router-dom';

import Images from './Images.js';
import SingleImage from './SingleImage.js';
import Navigator from './Navigator.js';

import './MainPage.css';

function MainPage() {
  const [imageLink, setImageLink] = useState('');
  const [author, setAuthor] = useState('');
  const [backArrow, setBackArrow] = useState(false);
  const Routes = useRoutes([
    {
      path: '/',
      element: (
        <>
          <Navigator arrow={backArrow} />
          <Images dataSetter={dataSetter} arrowSetter={setBackArrow} />
        </>
      ),
    },
    {
      path: '/singleImage',
      element: (
        <>
          <Navigator arrow={backArrow} />
          <SingleImage
            image={imageLink}
            author={author}
            arrowSetter={setBackArrow}
          />
        </>
      ),
    },
  ]);

  function dataSetter(image, author) {
    var link = '' + image;

    var width, height;

    var charTest = link.substr(26, 1);
    if (charTest === '/') {
      width = parseInt(link.substr(27, 3));
      height = parseInt(link.substr(31, 3));
    } else {
      width = parseInt(link.substr(28, 3));
      height = parseInt(link.substr(32, 3));
    }

    var aspectRatio = width / height;

    var newWidth = 750;
    var newHeight = parseInt(newWidth / aspectRatio);

    link = link.replace(width, newWidth);
    link = link.replace(height, newHeight);

    setBackArrow(true);
    setImageLink(link);
    setAuthor(author);
  }

  return Routes;
}

export default MainPage;
