import { memo } from 'react';
import { Masonry } from 'react-virtualized';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

const MasonryComponent = ({
  itemsWithSizes,
  columnWidth,
  defaultHeight,
  CellMeasurer,
  cache,
  dataSetter,
  cellPositioner,
  windowWidth,
}) => {
  const cellRenderer = ({ index, key, parent, style }) => {
    const { item, size } = itemsWithSizes[index];
    const height = columnWidth * (item.height / item.width) || defaultHeight;

    return (
      <CellMeasurer cache={cache} index={index} key={key} parent={parent}>
        <div style={style}>
          <motion.div
            whileHover={{ scale: 1.5, position: 'relative', zIndex: '1' }}
          >
            <Link to='/singleImage'>
              <img
                src={item.url}
                alt={item.title}
                style={{
                  height: height,
                  width: columnWidth,
                }}
                onClick={() => dataSetter(item.url, item.title)}
              />
            </Link>
          </motion.div>
          <h4>{item.title}</h4>
        </div>
      </CellMeasurer>
    );
  };

  return (
    <Masonry
      cellCount={itemsWithSizes.length}
      cellMeasurerCache={cache}
      cellPositioner={cellPositioner}
      cellRenderer={cellRenderer}
      height={window.innerHeight}
      width={windowWidth}
    />
  );
};

export default memo(MasonryComponent);
