import { React } from 'react';
import { Link } from 'react-router-dom';

import './Navigator.css';

import arrowPic from '../images/arrow.png';

const Navigator = ({ arrow }) => {
  return (
    <div className='Navigator'>
      {arrow === true && (
        <Link to='/'>
          <img
            src={arrowPic}
            style={{ width: '60px', height: '100%', backgroundColor: 'white' }}
          />
        </Link>
      )}
      <h4 className='Navigator__header'>React Album Prototype</h4>
    </div>
  );
};

export default Navigator;
